import { TPost } from '@/types'
import clsx from 'clsx'
import Image from '../image'
import { Link, useLocation } from '@tanstack/react-router'
import { FC } from 'react'
import PostTypeFeaturedIcon from './PostTypeFeaturedIcon'
// import PostTypeFeaturedIcon from './PostTypeFeaturedIcon'
// import GallerySlider from './GallerySlider'
// import MediaAudio from './MediaAudio'
import MediaVideo from './MediaVideo'
import { useIsPWA } from '@/hooks/useIsPWA'

interface Props {
  className?: string
  post: TPost
  isHover?: boolean
  vr: 'ar' | 'fr'
  setIsHover: () => void
}

const PostFeaturedMedia: FC<Props> = ({
  className,
  post,
  isHover = false,
  setIsHover,

  vr,
}) => {
  const { featuredImage, postType, videoUrl, handle, title, id } = post
  const path = vr === 'fr' ? '/news/fr/$postId' : '/news/$postId'
  const link = decodeURIComponent(handle)
  const location = useLocation()

  // const renderPostGallery = () => {
  //   if (!galleryImgs) {
  //     return renderImage()
  //   }

  //   return <GallerySlider handle={handle} galleryImgs={galleryImgs} />
  // }
  //

  const renderImage = () => {
    const isPWA = useIsPWA()
    return (
      <Link
        aria-label={title}
        to={path}
        params={{ postId: link }}
        search={{ id: id }}
        {...(!isPWA && {
          target: '_blank',
        })}
        onClick={() => {
          if (
            decodeURIComponent(location.pathname) ===
            path.replace('$postId', link)
          ) {
            window.scrollTo({ top: 0, behavior: 'smooth' })
          }
        }}
      >
        <Image
          alt={title}
          fill
          className="object-cover"
          src={featuredImage}
          sizes="(max-width: 600px) 100vw, 50vw"
        />
        <div className="absolute inset-0 bg-black/25 opacity-0 transition-opacity group-hover:opacity-100" />
      </Link>
    )
  }

  const renderPostVideo = () => {
    if (!videoUrl) {
      return (
        <>
          {renderImage()}
          <PostTypeFeaturedIcon
            onClick={setIsHover}
            className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2"
            postType={postType}
          />
        </>
      )
    }

    return (
      <>
        {renderImage()}
        {!isHover && (
          <PostTypeFeaturedIcon
            onClick={setIsHover}
            className="absolute z-30  top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 hover:bg-accent-foreground rounded-full cursor-pointer"
            postType={postType}
          />
        )}
        <MediaVideo
          setIsHover={setIsHover}
          isHover={isHover}
          videoUrl={videoUrl}
          handle={handle}
          vr={vr}
        />
      </>
    )
  }

  // const renderPostAudio = () => {
  //   return (
  //     <>
  //       {renderImage()}
  //       {audioUrl && <MediaAudio post={post} />}
  //       {!audioUrl && (
  //         <PostTypeFeaturedIcon
  //           className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2"
  //           postType={postType}
  //         />
  //       )}
  //     </>
  //   )
  // }

  return (
    <div className={clsx('relative size-full overflow-hidden', className)}>
      {/*{postType === 'gallery' && renderPostGallery()}*/}
      {postType === 'video' && renderPostVideo()}
      {/* {postType === 'audio' && renderPostAudio()}*/}
      {postType !== 'audio' &&
        postType !== 'video' &&
        postType !== 'gallery' &&
        renderImage()}
    </div>
  )
}

export default PostFeaturedMedia
