import MySlider from './MySlider'
import { ArrowRightIcon } from '@heroicons/react/24/outline'
import { Link } from '@tanstack/react-router'
import { FC } from 'react'
import Card10V2 from './Card10V2'
import { Heading, HeadingWithSubProps } from '@/shared/Heading'
import { TPost } from '@/types'

export interface Props
  extends Pick<HeadingWithSubProps, 'subHeading' | 'dimHeading'> {
  posts: TPost[]
  heading?: string
  className?: string
  subHeading?: string
  vr: 'ar' | 'fr'
}

export interface SectionSliderPostsProps extends Props {
  perView?: 2 | 3 | 4
  showViewAll?: boolean
  viewAllLinkHref?: string // or a strongly typed route path
}

const SectionSliderPosts: FC<SectionSliderPostsProps> = ({
  className = '',
  posts,
  perView = 4,
  showViewAll = true,
  viewAllLinkHref = '/trending',
  vr,
  heading,
}) => {
  const postsFiltered = posts

  const renderViewAllBtn = () => {
    if (!viewAllLinkHref) return null

    return (
      <div className="nc-Card10V2_viewAll relative flex h-full flex-col">
        <div className="aspect-w-16 relative w-full flex-shrink-0 aspect-h-12 sm:aspect-9">
          <Link
            aria-label="Voir tout"
            to={viewAllLinkHref}
            className="absolute inset-0 flex h-full w-full items-center justify-center rounded-3xl border-2 border-neutral-200 p-2 text-base font-medium underline transition-colors hover:border-neutral-300 hover:bg-neutral-50 dark:border-neutral-600 dark:hover:border-neutral-500 dark:hover:bg-neutral-800"
          >
            {vr === 'fr' ? 'Voir tout' : 'مشاهدة الكل'}
            <ArrowRightIcon className="ms-2.5 h-5 w-5 rtl:rotate-180" />
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className={`nc-SectionSliderPosts ${className}`}>
      {!!heading && <Heading className="relative mb-4 mx-1">{heading}</Heading>}
      <MySlider
        isFr={vr === 'fr'}
        data={showViewAll ? [...postsFiltered, 'viewAll'] : postsFiltered}
        renderItem={(item) => {
          if (typeof item === 'string') {
            return renderViewAllBtn()
          }
          return (
            <Card10V2 key={item.id} post={item} ratio="aspect-8/6" vr={vr} />
          )
        }}
        itemPerRow={perView}
      />
    </div>
  )
}

export default SectionSliderPosts
