import { TPost } from '@/types'
import clsx from 'clsx'
import { Link, useLocation } from '@tanstack/react-router'
import { FC } from 'react'
// import LocalDate from '../LocalDate'
import CategoryBadgeList from './CategoryBadgeList'
import PostCardSaveBtn from './PostCardSaveBtn'

interface Props {
  meta: Pick<
    TPost,
    'date' | 'title' | 'handle' | 'categories' | 'id' | 'short_link'
  >
  className?: string
  vr: 'ar' | 'fr'
}

const PostCardMeta2: FC<Props> = ({ meta, className, vr }) => {
  const { title, handle, categories, id, short_link } = meta
  const path = vr === 'fr' ? '/news/fr/$postId' : '/news/$postId'
  const link = decodeURIComponent(handle)
  const location = useLocation()

  return (
    <div
      className={clsx(
        'post-card-meta-2 relative flex gap-2 text-xs/6',
        className,
      )}
    >
      <div className="w-full">
        <span className="flex items-center gap-2 -mt-2 mb-2 pr-1">
          <CategoryBadgeList categories={categories} vr={vr} />
          <PostCardSaveBtn className="ms-auto" handle={short_link} vr={vr} />
        </span>

        <h2 className={clsx('block text-base font-semibold')}>
          <Link
            aria-label={title}
            onClick={() => {
              if (
                decodeURIComponent(location.pathname) ===
                path.replace('$postId', link)
              ) {
                window.scrollTo({ top: 0, behavior: 'smooth' })
              }
            }}
            to={path}
            search={{ id: id }}
            params={{ postId: link }}
            className="line-clamp-2"
            dangerouslySetInnerHTML={{ __html: title }}
          >
            {/*{title}*/}
          </Link>
        </h2>
      </div>
    </div>
  )
}

export default PostCardMeta2
